.l-modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: flex-start;
  justify-content: center;
  padding: var(--ls-padding-lg);
  overflow-y: auto;
  z-index: 1000;
}
.l-modal--centered {
  align-items: center;
}
.l-modal--fullscreen {
  padding: 0;
}
.l-modal__dialog {
  position: relative;
  background-color: var(--ldesign-bg-color-container);
  border-radius: var(--ls-border-radius-lg);
  box-shadow: var(--ldesign-shadow-3);
  max-width: 100%;
  max-height: 100%;
  display: flex;
  flex-direction: column;
  margin: auto 0;
}
.l-modal__dialog--small {
  width: 400px;
}
.l-modal__dialog--medium {
  width: 520px;
}
.l-modal__dialog--large {
  width: 720px;
}
.l-modal__dialog--fullscreen {
  width: 100vw;
  height: 100vh;
  max-width: none;
  max-height: none;
  border-radius: 0;
  margin: 0;
}
.l-modal__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: var(--ls-padding-base) var(--ls-padding-base) 0;
  border-bottom: 1px solid var(--ldesign-border-level-1-color);
  margin-bottom: var(--ls-margin-base);
}
.l-modal__title {
  font-size: var(--ls-font-size-lg);
  font-weight: 600;
  color: var(--ldesign-text-color-primary);
  margin: 0;
  line-height: 1.4;
}
.l-modal__close {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 32px;
  height: 32px;
  border: none;
  background: transparent;
  border-radius: var(--ls-border-radius-base);
  color: var(--ldesign-text-color-secondary);
  font-size: 18px;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
}
.l-modal__close:hover {
  background-color: var(--ldesign-bg-color-component-hover);
  color: var(--ldesign-text-color-primary);
}
.l-modal__close:active {
  background-color: var(--ldesign-bg-color-component-active);
}
.l-modal__body {
  flex: 1;
  padding: 0 var(--ls-padding-base);
  color: var(--ldesign-text-color-primary);
  line-height: 1.6;
  overflow-y: auto;
}
.l-modal__body--no-padding {
  padding: 0;
}
.l-modal__footer {
  padding: var(--ls-padding-base);
  border-top: 1px solid var(--ldesign-border-level-1-color);
  margin-top: var(--ls-margin-base);
  display: flex;
  justify-content: flex-end;
  gap: var(--ls-spacing-sm);
}
.l-modal-enter-active,
.l-modal-leave-active {
  transition: opacity 0.3s ease-in-out;
}
.l-modal-enter-active .l-modal__dialog,
.l-modal-leave-active .l-modal__dialog {
  transition: transform 0.3s ease-in-out;
}
.l-modal-enter-from,
.l-modal-leave-to {
  opacity: 0;
}
.l-modal-enter-from .l-modal__dialog,
.l-modal-leave-to .l-modal__dialog {
  transform: scale(0.9) translateY(-20px);
}
.l-modal-enter-to,
.l-modal-leave-from {
  opacity: 1;
}
.l-modal-enter-to .l-modal__dialog,
.l-modal-leave-from .l-modal__dialog {
  transform: scale(1) translateY(0);
}
